<template>
	<!-- sunui-uploader-bd -->
	<view class=""> 
		<view class="sunui-uploader-files">
			<block v-for="(item,index) in upload_before_list" :key='index'>
				<view class="sunui-uploader-file" :class="[item.upload_percent<100?'sunui-uploader-file-status':'']" @click="previewImage(index)">
					<image class="sunui-uploader-img" :style="upload_img_wh" :src="item.path" mode="aspectFill" />
					<view class="sunui-img-removeicon right" @click.stop="removeImage(index)" v-show="upimg_move">×</view>
					<view class="sunui-loader-filecontent" v-if="item.upload_percent<100">{{item.upload_percent}}%</view>
				</view>
			</block>
			<view v-if="upload_before_list.length<upload_count" hover-class="sunui-uploader-hover" class="sunui-uploader-inputbox"
			 @click="chooseImage" :style="upload_img_wh">
				<view>
					<text class="iconfont icon-mn_shangchuantupian" style="color: #666;"></text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				upload_cache: [],
				upload_cache_list: [],
				upload_before_list:[]
			}
		},
		name: 'sunui-upimg',
		props: {
			// 服务器url
			url: {
				type: String,
				default: 'https://a3.dns06.net.cn/app/index.php?i=2&c=entry&a=wxapp&do=Upload_qiniu_b&m=jzwx_a'
			},
			// 上传样式宽高
			upload_img_wh: {
				type: String,
				default: 'width:162rpx;height:162rpx;'
			},
			// 上传数量
			upload_count: {
				type: Number,
				default: 9
			},
			// 是否自动上传? 可以先用变量为false然后再改为true即为手动上传
			upload_auto: {
				type: Boolean,
				default: true
			},
			// 是否显示删除
			upimg_move: {
				type: Boolean,
				default: true
			}
		},
		async created() {
			let _self = this;
			setTimeout(function() {
				_self.upload_before_list.map(function(item) {
					_self.upload_cache_list.push(item.path);
				});
				_self.emit();
			}, 300);
		},
		methods: {
			upImage(paths) {
				let _self = this;
				const promises = paths.map(function(path) {
					return promisify(upload)({
						url: _self.url,
						path: path,
						name: 'file',
						extra: {},
						_self: _self
					})
				});

				uni.showLoading({
					title: `正在上传...`,
				});

				Promise.all(promises).then(function(data) {
					uni.hideLoading();
					_self.upload_cache_list.push(...data);
					_self.emit();
				}).catch(function(res) {
					uni.hideLoading();
				});
			},
			chooseImage() {
				let _self = this;
				uni.chooseImage({
					count: _self.upload_count - _self.upload_before_list.length,
					sizeType: ['compressed', 'original'],
					sourceType: ['album', 'camera'],
					success:  function(res) {
						for (let i = 0, len = res.tempFiles.length; i < len; i++) {
							res.tempFiles[i]['upload_percent'] = 0;
							 _self.upload_before_list.push(res.tempFiles[i]);
						}
						_self.upload_cache =  res.tempFilePaths;
						_self.upload(_self.upload_auto);
					},
					fail: function(err) {
						console.log(err);
					}
				});
			},
			async upload(upload_auto) {
				let _self = this;
				upload_auto ? await _self.upImage(_self.upload_cache) : console.warn(
					`传输参数:this.$refs.xx.upload(true)才可上传,默认false`);
			},
			previewImage(idx) {
				let _self = this;
				let preview = [];
				for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
					preview.push(_self.upload_before_list[i].path);
				}
				uni.previewImage({
					current: idx,
					urls: preview
				});
			},
			removeImage(idx) {
				let _self = this;
				_self.upload_before_list.splice(idx, 1);
				_self.upload_cache_list.splice(idx, 1);
				_self.emit();
			},
			emit() {
				let _self = this;
				_self.$emit('change', _self.upload_cache_list);
			}
		}
	}


	const promisify = (api) => {
		return function(options, ...params) {
			return new Promise(function(resolve, reject) {
				api(Object.assign({}, options, {
					success: resolve,
					fail: reject
				}), ...params);
			});

		}
	}

	const upload = function(options) {
		let url = options.url,
			_self = options._self,
			path = options.path,
			name = options.name,
			// data = options.data,
			extra = options.extra,
			success = options.success,
			progress = options.progress,
			fail = options.fail
			
		// 创建文件
		var file = new _self.$av.File(name, {
				blob: {
					uri: path	// path中为临时文件，转为字节文件才可以上传
				}
			})
		// 保存文件
		file.save({
			// 上传进度监控
			onprogress: function(progress) {
				for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
					_self.upload_before_list[i]['upload_percent'] = Number(progress.percent).toFixed(2);
				}
				_self.upload_before_list = _self.upload_before_list;
			}
		}).then(function(file) {
			console.log('文件保存完成,url: ' + file.attributes.url)
			success(file.attributes.url)	// 上传成功后，返回真实URL，替换掉原来的临时URL
		}, function(error) {
			console.log('文件保存失败!')
			file(error)
		})
		// const uploadTask = uni.uploadFile({
		// 	url: url,
		// 	filePath: path,
		// 	name: name,
		// 	formData: extra,
		// 	success: function(res) {
		// 		var data = res.data
		// 		try {
		// 			//Tip : 切记->主要修改这里图片的返回值为真实返回路径!!! 详情见示例
		// 			data = JSON.parse(res.data).info;
		// 		} catch (e) {
		// 			throw (e, data)
		// 		}
		// 		// 根据自己的返回数据做相应判断,服务器返回200即代表成功请求
		// 		if (res.statusCode == 200) {
		// 			if (success) {
		// 				success(data)
		// 			}
		// 		} else {
		// 			if (fail) {
		// 				fail(data)
		// 			}
		// 		}

		// 	},
		// 	fail: function(res) {
		// 		console.log(res)
		// 		if (fail) {
		// 			fail(res)
		// 		}
		// 	}
		// })
		// uploadTask.onProgressUpdate(async function(res) {
		// 	for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
		// 		_self.upload_before_list[i]['upload_percent'] = await res.progress;
		// 	}
		// 	_self.upload_before_list = _self.upload_before_list;
		// });
	}
</script>

<style lang="scss">
	@font-face {
		font-family: "iconfont";
		src: url('//at.alicdn.com/iconfont.eot?t=1574391686418');
		/* IE9 */
		src: url('//at.alicdn.com/iconfont.eot?t=1574391686418#iefix') format('embedded-opentype'),
			/* IE6-IE8 */
			url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
			url('//at.alicdn.com/iconfont.woff?t=1574391686418') format('woff'),
			url('//at.alicdn.com/iconfont.ttf?t=1574391686418') format('truetype'),
			/* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
			url('//at.alicdn.com/iconfont.svg?t=1574391686418#iconfont') format('svg');
		/* iOS 4.1- */
	}

	.iconfont {
		font-family: "iconfont" !important;
		font-size: 16px;
		font-style: normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	.icon-mn_shangchuantupian:before {
		content: "\e559";
	}

	.icon-mn_shangchuantupian {
		font-size: 3em;
	}

	.sunui-uploader-img {
		display: block;
	}

	.sunui-uploader-input {
		position: absolute;
		z-index: 1;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
	}

	.sunui-uploader-inputbox {
		position: relative;
		margin-bottom: 16rpx;
		box-sizing: border-box;
		background-color: #ededed;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
	}

	.sunui-img-removeicon {
		position: absolute;
		color: #fff;
		width: 40upx;
		height: 40upx;
		line-height: 40upx;
		z-index: 2;
		text-align: center;
		background-color: #E54D42;

		&.right {
			top: 0;
			right: 0;
		}
	}

	.sunui-uploader-file {
		position: relative;
		margin-right: 16rpx;
		margin-bottom: 16rpx;
	}

	.sunui-uploader-file-status:before {
		content: " ";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.5);
	}

	.sunui-loader-filecontent {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: #fff;
		z-index: 9;
	}

	.sunui-uploader-bd {
		padding: 26rpx;
		margin: 0;
	}

	.sunui-uploader-files {
		display: flex;
		flex-wrap: wrap;
	}

	.sunui-uploader-file:nth-child(4n+0) {
		margin-right: 0;
	}

	.sunui-uploader-inputbox {
		&>view {
			text-align: center;
		}
	}

	.sunui-uploader-file-status:after {
		content: " ";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.5);
	}

	.sunui-uploader-hover {
		box-shadow: 0 0 0 #e5e5e5;
		background: #e5e5e5;
	}
</style>
